@import url("https://fonts.googleapis.com/css2?family=Roboto+Condensed:wght@400;700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;700&display=swap");

:root {
	--dark-primary: #000f20;
	--dark-primary-alt: #002249;
	--dark-secondary: #80b9ff;
	--dark-secondary-alt: #a2ccff;
	--light-primary: #ececff;
	--light-primary-alt: #c4c4dd;
	--light-secondary: #002550;
	--light-secondary-alt: #003f88;
}

* {
	box-sizing: border-box;
	margin: 0;
}

html {
	scroll-behavior: smooth;
}

body {
	user-select: none;
	font-family: "Roboto Condensed", Arial, Helvetica, sans-serif;
	font-size: 20px;
	font-weight: 500;
	width: 100vw;
	overflow-x: hidden;
}

header, section, footer {
	width: 100vw;
}

header, footer {
	display: grid;
    align-content: center;
    align-items: center;
	height: 55px;
	z-index: 10;
}

.dark-header-footer {
	background-color: var(--dark-primary-alt);
	color:var(--dark-secondary);
	box-shadow: 0 0 50px 5px var(--dark-secondary);
}

.light-header-footer {
	background-color: var(--light-primary-alt);
	color:var(--light-secondary);
	box-shadow: 0 0 50px 5px var(--light-secondary);
}

header {
	grid-template-columns: auto auto;
    justify-content: space-between;
	position: fixed;
	padding: 0 20px 0 70px;
	border-radius: 0 0 0 28px;
}

footer {
    justify-content: center;
	font-size: 16px;
}

#gear {
	position: fixed;
	cursor: pointer;
	z-index: 20;
}

#back-to-top {
	width: 45px;
	height: 45px;
	right: 40px;
	border-radius: 50%;
	transition: bottom 0.5s ease-out;
}

.dark-svg-fill {
	background-color: var(--dark-primary);
	color: var(--dark-secondary);
	fill: var(--dark-secondary);
}

.dark-svg-fill:hover {
	color: var(--dark-secondary-alt);
	fill: var(--dark-secondary-alt);
}

.light-svg-fill {
	background-color: var(--light-primary);
	color: var(--light-secondary);
	fill: var(--light-secondary);
}

.light-svg-fill:hover {
	color: var(--light-secondary-alt);
	fill: var(--light-secondary-alt);
}

.dark-svg-stroke {
	stroke: var(--dark-secondary);
	fill: var(--dark-primary);
}

.dark-svg-stroke:hover {
	stroke: var(--dark-secondary-alt);
}

.light-svg-stroke {
	stroke: var(--light-secondary);
	fill: var(--light-primary);
}

.light-svg-stroke:hover {
	stroke: var(--light-secondary-alt);
}

.dark-main {
	background-color: var(--dark-primary);
	color:var(--dark-secondary);
	text-shadow: 0 0 10px #6200ff;
}

.light-main {
	background-color: var(--light-primary);
	color:var(--light-secondary);
	text-shadow: 0 0 10px var(--light-primary-alt);
}

section {
	display: grid;
	align-content: center;
    justify-items: center;
	gap: 30px;
	height: auto;
	padding: 150px 20px 50px 20px;
}

nav {
	display: grid;
	grid-template-columns: repeat(4, auto);
    align-items: center;
	gap: 0;
	font-family: "Poppins", Arial, Helvetica, sans-serif;
}

a, a:active, a:visited {
	color: inherit;
	text-decoration: none;
}

nav > a {
	padding: 10px 2vw;
	transition: color 0.5s, text-shadow 0.5s, opacity 0.5s;
}

nav:hover > a {
	opacity: 0.25;
	transition: opacity 0.5s;
}

nav > a:hover {
	opacity: 1;
	transition: color 0.5s, text-shadow 0.5s, opacity 0.5s;
}

.dark-header-footer > nav > a:hover {
	color: var(--dark-secondary-alt);
	text-shadow: 0 0 25px var(--dark-secondary);
}

.light-header-footer > nav > a:hover {
	color: var(--light-secondary-alt);
	text-shadow: 0 0 35px var(--light-secondary-alt); 
}

#logo {
	font-size: 30px;
	font-weight: 700;
}

.menu-button {
	display: none;
}

.menu-button > div {
	width: 35px; 
	height: 5px;
	border-radius: 2.5px;
	margin: 5px 0;
	transition: transform 0.5s, opacity 0.5s;
}

.dark-menu-button {
	background-color: var(--dark-secondary);
}

.light-menu-button {
	background-color: var(--light-secondary);
}

h1 {
	text-align: center;
	font-family: "Poppins", Arial, Helvetica, sans-serif;
	font-size: 40px;
	font-weight: 500;
}

h2 {
	text-align: center;
	font-size: 30px;
	font-weight: 500;
}

span {
	font-family: "Poppins", Arial, Helvetica, sans-serif;
	font-weight: 700;
}

#topH {
	font-family: "Roboto Condensed", Arial, Helvetica, sans-serif;
}

#middleH {
	font-family: "Poppins", Arial, Helvetica, sans-serif;
	font-weight: 500;
}

ul > li {
	padding-bottom: 10px;
}

#welcome {
	min-height: 600px;
	height: 100vh;
}

#avatar-image {
	height: 50vw;
	max-height: 250px;
	border-radius: 50%;
}

.dark-avatar {
	background-color: var(--dark-secondary);
	box-shadow: 0 0 20px 5px var(--dark-secondary);
	border: 10px solid var(--dark-secondary);
}

.light-avatar {
	background-color: var(--light-secondary);
	box-shadow: 0 0 20px 5px var(--light-secondary);
	border: 10px solid var(--light-secondary);
}

#skills > div > h2 {
	padding-bottom: 10px;
}

.icon-collection {
	width: 220px;
	height: 50px;
	margin: 0 auto;
}

.icon-container {
	display: inline-block;
	height: 50px;
	width: 50px;
	padding: 5px;
	border-radius: 5px;
	margin: 0 2.5px;
}

.icon-tooltip {
	position: absolute;
	opacity: 0;
	font-family: "Roboto Condensed", Arial, Helvetica, sans-serif;
	font-size: 14px;
	font-weight: 300;
	text-align: center;
	text-shadow: none;
	padding: 3px 6px;
	border-radius: 3px;
}

.dark-icon-tooltip {
	background-color: var(--dark-secondary);
	color: var(--dark-primary);
}

.light-icon-tooltip {
	background-color: var(--light-secondary);
	color: var(--light-primary);
}

.icon-image {
	height: 40px;
	width: 40px;
}

#projects {
	gap: 0;
	padding: 100px 0 100px 0;
}

.arrow-button {
	position: relative;
	height: 75px;
	cursor: pointer;
	z-index: 5;
}

#projects-container {
	height: 550px;
	overflow: hidden;
}

#project-slider {
	display: grid;
	grid-template-columns: repeat(6, auto);
	gap: 50px;
	position: relative;
	top: 25px;
	transition: left 0.5s;
}

.project-box {
	display: grid;
	grid-template-rows: 60px 30px 140px 210px 50px;
	gap: 2.5px;
	width: 250px;
	height: 500px;
	border-radius: 7px;
}

.dark-project-box {
	box-shadow: 0 0 15px 5px var(--dark-secondary);
}

.light-project-box {
	box-shadow: 0 0 15px 5px var(--light-secondary);
}

.project-title {
	text-align: center;
	margin: auto;
}

.project-techs {
	margin: auto;
}

.project-techs > svg {
	width: 20px;
	height: 20px;
	margin-right: 5px;
}

.project-techs > img:last-child {
	margin-right: 0px;
}

.project-img {
	width: calc(100% - 16px);
	margin: 8px;
}

.dark-project-box > .project-img {
	border: 1px solid var(--dark-secondary);
}

.light-project-box > .project-img {
	border: 1px solid var(--light-secondary);
}

.project-description {
	font-size: 16px;
	padding: 5px 10px;
}

.project-buttons {
	margin: auto;
}

.project-buttons > a {
	display: inline-block;
	text-align: center;
	text-shadow: none;
	padding: 4px 0;
	border-radius: 5px;
}

.dark-button {
	background-color: var(--dark-primary);
	color: var(--dark-secondary);
	border: 2px solid var(--dark-secondary);
}

.dark-button:hover {
	background-color: var(--dark-secondary);
	color: var(--dark-primary);
}

.light-button {
	background-color: var(--light-primary);
	color: var(--light-secondary);
	border: 2px solid var(--light-secondary);
}

.light-button:hover {
	background-color: var(--light-secondary);
	color: var(--light-primary);
}

.project-demo-button {
	width: 176px;
	margin-right: 8px;
}

.project-code-button {
	width: 50px;
}

#contact {
	padding: 150px 20px;
}

#contacts > div {
	margin-bottom: 12px;
}

.contact-image {
	width: 40px;
	vertical-align: middle;
}

.contact-text {
	font-family: "Roboto Condensed", Arial, Helvetica, sans-serif;
	font-weight: 500;
	vertical-align: middle;
	padding-left: 10px;
}

.visible {
	opacity: 1;
	transition: opacity 0.5s;
}

.invisible {
	opacity: 0;
	transition: opacity 0.5s;
}

@media only screen and (max-width: 650px) {
	body {
		font-size: 16px;
	}

	header, footer {
		height: 45px;
	}

	header {
		padding-left: 56px;
		padding-right: 10px;
		border-radius: 0 0 0 22.5px;
	}

	#back-to-top {
		right: -100px;
	}

	section {
		gap: 24px;
	}

	#logo {
		font-size: 24px;
	}

	nav {
		display: none;
		grid-template-columns: auto;
		gap: 0;
		position: fixed;
		top: 45px;
		right: 0px;
		width: auto;
	}

	.dark-mini-nav {
		background-color: var(--dark-primary);
		border: 1px solid var(--dark-secondary);
	}

	.light-mini-nav {
		background-color: var(--light-primary-alt);
		border: 1px solid var(--light-secondary);
	}

	nav > a {
		text-align: right;
		width: auto;
		padding: 7.5px 15px 7.5px 15px;
	}

	.dark-mini-nav-a:hover {
		background-color: var(--dark-secondary);
		color: var(--dark-primary);
	}

	.light-mini-nav-a:hover {
		background-color: var(--light-secondary);
		color: var(--light-primary);
	}

	.menu-button {
		display: inline-block;
	}

	footer {
		font-size: 12px;
	}
	
	h1 {
		font-size: 32px;
	}
	
	h2 {
		font-size: 24px;
	}

	#avatar-image {
		border-width: 2vw;
	}

	.icon-collection {
		width: 176px;
		height: 48px;
	}
	
	.icon-container {
		height: 40px;
		width: 40px;
		padding: 4px;
		border-radius: 4px;
		margin: 0 2px;
	}
	
	.icon-image {
		height: 32px;
		width: 32px;
	}

	.arrow-button {
		height: 60px;
	}

	#projects-container {
		height: 440px;
	}

	#project-slider {
		gap: 40px;
		top: 20px;
	}

	.project-box {
		grid-template-rows: 48px 24px 112px 168px 40px;
		gap: 2px;
		width: 200px;
		height: 400px;
		border-radius: 7px;
	}

	.project-techs > img {
		width: 16px;
		height: 16px;
		margin-right: 4px;
	}

	.project-description {
		font-size: 12px;
		padding: 5px 10px;
	}

	.project-buttons > a {
		padding: 2.5px 0;
	}

	.project-demo-button {
		width: 139px;
		margin-right: 7px;
	}

	.project-code-button {
		width: 40px;
	}

	.contact-image {
		width: 32px;
	}
}

@media only screen and (max-width: 380px) {
	header {
		border-radius: 0;
	}

	nav {
		grid-template-columns: auto;
		top: 45px;
		width: 100vw;
		height: auto;
	}

	nav > a {
		text-align: center;
		width: 100vw;
		padding-top: 7.5px;
		padding-bottom: 7.5px;
	}
}